  width: 100%;
  height: 100%;
  z-index: 20;
}

.loading::after {
  content: '';
  display: block;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -16px;
  margin-left: -16px;
  /* Spinner Circle Rotation */
  border: 4px rgba(0, 0, 0, 0.25) solid;
  border-top: 4px black solid;
  border-radius: 50%;
  -webkit-animation: spCircRot .75s infinite linear;
  animation: spCircRot .75s infinite linear;
}

